
.one {
    float: left;
}
.two {
    clear: left;
}

/* card */
#container1 {
    width: 90%;
    height: fit-content;
    margin: auto;
    align-items: center;
}
.card {
    width: 300px;
    height: 500px;
    margin: 1rem;
    float: left;
    box-shadow: rgba(0, 0, 0, 0.2) 0px 1px 3px, rgba(0, 0, 0, 0.3) 0px 1px 2px;
}

.card-body > .card-title {
    width: 50%;
    float: left;
}
#h5_card{
    clear: left;
    color: rgb(241, 109, 109);
}
.card-img-top {
    height: 350px;
}
.card-body > .btn {
    width: 50%;
}

.modal, .modal-dialog  {
    width: 100%;
}

.modal-header, .modal-footer{
    background-color: rgb(240, 122, 122);
    color: white;
    font-weight: bold;
}
.modal-body {
    display: flex;
    justify-content: space-evenly;
}
.modal-body > img, p {
    width: 45%;
    margin-left: 0;
}

/* pagination */
#row {
    margin: auto;
}
.nav {
    clear: left;
}
#resetbutton {
    margin-left: 2rem;
}
.form-control {
    width: 30%;
}


/* carousel */
.carousel-item {
    height: 40%;
    margin: auto;
}
.carousel-item > img {
    width: 60%;
    margin: 10% 20%;
    box-shadow: rgba(0, 0, 0, 2) 0px 5px 24px 0px, rgba(0, 0, 0, 2) 0px 0px 0px 1px;
}
/* carousel button */
.carousel-control-prev, .carousel-control-next {
    max-width: 10%;
    max-height: 10%;
    margin: auto 5%;
    border-radius: 50%;
    box-shadow: rgba(0, 0, 0, .2) 0 3px 5px -1px,rgba(0, 0, 0, .14) 0 6px 10px 0,rgba(0, 0, 0, .12) 0 1px 18px 0;
}
/* div cuá»‘i */
#container2 {
    width: 90%;
    height: 500px;
    margin: auto;
    padding: 2rem;
}

#heading2{
    width: 250px;
    margin-left: 0;
    background-color: rgb(1, 133, 138);
    border: 10px solid rgb(201, 29, 29);
    border-radius: 30px;
    color: white;
    text-align: center;
}
#list {
    height: 0;
    margin: 0;
    padding: 0;
    transition: height 600ms ease-out;
    overflow: hidden;
}
#heading2:hover + #list {
    height: 200px;
}
#list:hover {
    height: 200px;
}
#list > li {
    padding:0.5rem;
    margin: 0.5rem;
    font-size: 20px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    color: brown;
    list-style-type: none;
    box-shadow: rgba(117, 70, 248, 0.3) 0px 1px 2px 0px, rgba(117, 70, 248, 0.15) 0px 1px 3px 1px;
}

#div {
    height: 300px;
    min-width: 500px;
    background-color: rgb(102, 14, 143);
    border-radius: 30px;
    box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}
#div > p {
    margin: auto;
    width: 40%;
    color: white;
    font-size: 1rem;
    font-family: monospace;
    float: left;
}
#div > iframe {
    margin: auto;
    width: 40%;
    height: 250px;
}
